<template>
  <div>
    <h4>CHANGE SEVERITY</h4>

    <p class="note lowmargin">

&#9432; &nbsp; Note that if you have multiple SOAR actions for an incident, the incident will be set to the
highest severity specified by the user</p>

    <label class="switch" name="extensionEnabled" value="extensionEnabled"
      style="vertical-align: middle; margin-left: 4px;">
      <input v-model="this.increaseSevOnResults" type="checkbox" id="extensionEnabled" checked>
      <span class="slider round"></span>
    </label>
    <label style="vertical-align: middle; margin-left: 5px;" for="extensionEnabled"> Change incident severity if query
      returns results </label>


    
    
    
      <div v-if="this.increaseSevOnResults">
      <p class="inline">
        If your query returns <input type="number" id="sevChangeCount" min="1" style="width: 5em;" v-model="this.selectedSeverityEntityQueryMatchesVal" @input="this.generateDictSettings"> <b>results</b>,
      </p>
      <p class="inline" style="color:rgba(0, 120, 212, 0.822); font-weight: 900 ; font-size: 1.2em">
        &nbsp; THEN &nbsp;
      </p>
      <p class="inline">
        <b> set the priority to &nbsp;</b>
        <select name="rulesSeverity" id="rulesSeverity" v-model="this.selectedSeverityEntityQueryMatches" @change="this.generateDictSettings">
          <option value="Informational">Informational</option>
          <option value="Low">Low</option>
          <option value="Medium">Medium</option>
          <option value="High">High</option>-
        </select>
      </p>
    </div>


    <br>

    <label class="switch" name="switchValueMatches" value="swtichValeMatches"
      style="vertical-align: middle; margin-left: 4px;">
      <input v-model="this.increaseifEntityContains" type="checkbox" id="extensionEnabled" checked>
      <span class="slider round"></span>
    </label>
    <label style="vertical-align: middle; margin-left: 5px;" for="extensionEnabled"> Change incident severity the entity value contains a value </label>


    
    
    
      <div v-if="this.increaseifEntityContains">
      <p class="inline">
        If the entity  <b>contains</b> the phrase <input type="text" id="sevChangeCount" min="1" style="width: 10em;" v-model="this.selectedSeverityEntityContainsVal" @input="this.generateDictSettings">
      </p>
      <p class="inline" style="color:rgba(0, 120, 212, 0.822); font-weight: 900 ; font-size: 1.2em">
        &nbsp; THEN &nbsp;
      </p>
      <p class="inline">
        <b> set the priority to &nbsp;</b>
        <select name="rulesSeverity" id="rulesSeverity" v-model="this.selectedSeverityEntityContains" @change="this.generateDictSettings">
          <option value="Informational">Informational</option>
          <option value="Low">Low</option>
          <option value="Medium">Medium</option>
          <option value="High">High</option>-
        </select>
      </p>
    </div>


  </div>
</template>

<script>
export default {
  name: 'SeverityChange',
  props: {
  },
  data() {
    return {
      // triggers to show elements on page when slider selected
      increaseSevOnResults: false,
      increaseifEntityContains: false,

      // bind to severity contains value
      selectedSeverityEntityContainsVal: null,
      selectedSeverityEntityContains: "Informational",

      // bind to if results return count
      selectedSeverityEntityQueryMatchesVal: null,
      selectedSeverityEntityQueryMatches:"Informational"

    };
  },
  methods: {
    // returns a dict that can be used to configs from parent components
    generateDictSettings: function () {
      let retVal = []

      if (this.increaseSevOnResults && this.selectedSeverityEntityQueryMatchesVal!=null && this.selectedSeverityEntityQueryMatches!=null ) {
        retVal.push({"results_count_mt": this.selectedSeverityEntityQueryMatchesVal , "severity": this.selectedSeverityEntityQueryMatches})
      }

      if (this.increaseifEntityContains) {
        retVal.push({"entity_contains":this.selectedSeverityEntityContainsVal, "severity":this.selectedSeverityEntityContains})
      }

      // if we have anything then return 
      if (retVal.length > 0) {
        this.$emit("severityConf",{
          type:"severity_change",
          value:retVal})
        return {"severity_change":retVal}
      } else {
        return null
      }


    }
  }

}
</script>

