<template>
  <div style="max-width: 30vw;">
    <button style="vertical-align: middle;" class="settingsButton" v-on:click="showSettings">
      <svg style="vertical-align: middle; width: 15px;" xmlns="http://www.w3.org/2000/svg" width="24" height="24"
        viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round"
        stroke-linejoin="round" class="feather feather-settings">
        <circle cx="12" cy="12" r="3"></circle>
        <path
          d="M19.4 15a1.65 1.65 0 0 0 .33 1.82l.06.06a2 2 0 0 1 0 2.83 2 2 0 0 1-2.83 0l-.06-.06a1.65 1.65 0 0 0-1.82-.33 1.65 1.65 0 0 0-1 1.51V21a2 2 0 0 1-2 2 2 2 0 0 1-2-2v-.09A1.65 1.65 0 0 0 9 19.4a1.65 1.65 0 0 0-1.82.33l-.06.06a2 2 0 0 1-2.83 0 2 2 0 0 1 0-2.83l.06-.06a1.65 1.65 0 0 0 .33-1.82 1.65 1.65 0 0 0-1.51-1H3a2 2 0 0 1-2-2 2 2 0 0 1 2-2h.09A1.65 1.65 0 0 0 4.6 9a1.65 1.65 0 0 0-.33-1.82l-.06-.06a2 2 0 0 1 0-2.83 2 2 0 0 1 2.83 0l.06.06a1.65 1.65 0 0 0 1.82.33H9a1.65 1.65 0 0 0 1-1.51V3a2 2 0 0 1 2-2 2 2 0 0 1 2 2v.09a1.65 1.65 0 0 0 1 1.51 1.65 1.65 0 0 0 1.82-.33l.06-.06a2 2 0 0 1 2.83 0 2 2 0 0 1 0 2.83l-.06.06a1.65 1.65 0 0 0-.33 1.82V9a1.65 1.65 0 0 0 1.51 1H21a2 2 0 0 1 2 2 2 2 0 0 1-2 2h-.09a1.65 1.65 0 0 0-1.51 1z">
        </path>
      </svg>
      &nbsp;Settings
    </button>
  </div>


  <div>
    <p style="color: orange; font-size: 0.8em">
      {{ this.displayWarningErrors }}
    </p>
  </div>


  <dialog id="settingDialog" class="settingsModal">
    <h3 class="settingsModal">Configure Options</h3>
    <br>
    <h4 class="settingsModal">Set function app URL (including the trigger name)</h4>
    <p style="margin-top: 0px; font-size: 0.8em;" class="settingsModal">
      <i>https://(yourdomain).azurewebsites.net/api/http_trigger1</i>
    </p>
    <input type="text" class="settingsModal" v-model="this.websiteUrl">

    <br>
    <br>

    <h4 class="settingsModal">Enter Function Key</h4>
    <p style="margin-top: 0px; font-size: 0.8em;" class="settingsModal">
      <i>Base64 encoded function key
      </i>
    </p>
    <input type="text" class="settingsModal" v-model="this.websiteKey">

    <br>
    <br>

    <button autofocus class="settingsButton" v-on:click="closeModal">Save</button>
  </dialog>

</template>

<script>


export default {
  name: 'AppSettings',
  props: {

  },
  data() {
    return {
      displayWarningErrors: "",
      websiteUrl: '',
      websiteKey: '',
    };
  },
  methods: {
    showSettings: function () {
      this.loadSettings()
      var e = document.getElementById('settingDialog')
      e.showModal()
    },

    saveSettings: function () {
      localStorage.setItem("SsegWebsiteUrl", this.websiteUrl)
      localStorage.setItem("SsegWebsiteKey", this.websiteKey)
      console.log("Saved settings")
    },

    loadSettings: function () {
      this.websiteUrl = localStorage.getItem("SsegWebsiteUrl")
      this.websiteKey = localStorage.getItem("SsegWebsiteKey")
      console.log("Opened settings")
    },


    closeModal: function () {
      this.saveSettings()
      this.settingsWarning()
      var e = document.getElementById('settingDialog')
      e.close()
    },

    settingsWarning: function () {
      if (this.websiteUrl == '' || this.websiteKey == '') {
        this.displayWarningErrors = "Configure function app URL. Entities may show discrepancies and jobs will not be validated upon creation."
      } else { this.displayWarningErrors = '' }

    }

  },
  mounted() {
    this.loadSettings()
    this.settingsWarning()


  }

}



</script>

