<template>
  <div style="margin-left:25px">

    <h2>Create Query</h2>

    <h4 style="display: inline-block;">Run this query against entity type: </h4>
    <p style="display: inline-block;"> &nbsp; {{ this.entityTypeSelected }}</p>

    <p style="color: gray;">&#9432; &nbsp; Use <i>%ENTITY%</i> within your query to substitute the entity value from the
      incident in your query
    </p>

    <p style="margin-bottom: 0px;">
      Recommended Syntax:
    </p>

    <p style="font-family: monospace; color: grey;">
      <i>
        let LookFor = "%ENTITY%";
        <br>
        SigninLogs | where UserPrincipalName == LookFor
      </i>
    </p>

    <textarea @input="$emit('queryText', this.reflectedStr)" v-model="this.reflectedStr" name="" id="" cols="100" rows="10"></textarea>
<p v-show="!this.reflectedStr.includes('%ENTITY%')" style="color: orange;"> Warning: your query does not contain and %ENTITY% substitution. </p>
    <br>
    <p v-if="this.sampleEntityValue != 'example'" style="display: inline-block;" >Value from the SOAR engine as it would appear in the query:</p><pre style="display: inline-block; color: rgb(0, 120, 212);" v-if="this.sampleEntityValue != 'example'"> <b> {{ this.sampleEntityValue }} </b> </pre>
    <pre v-show="!this.engineErrors.length > 0" style="color: red; white-space: pre-line;"> <b> {{ this.engineErrors }} </b></pre>

    <h3>Reflected Query</h3>
    <pre style="white-space: pre-line;"> {{ this.reflectedStr.replaceAll('%ENTITY%', this.sampleEntityValue) }} </pre>

  </div>


</template>

<script>


export default {
  name: 'CreateQuery',
  props: {
    selectedEntityId: {
      type: String,
      default: null,
      required: false
    },
    JsonString: {
      type: String,
      default: null,
      required: false
    },
    entityTypeSelected: {
      type: String,
      default: null,
      required: false
    },

  },
  data() {
    return {
      websiteUrl: '',
      websiteKey: '',
      reflectedStr: '',
      sampleEntityValue: 'example', // default if not enriching from engine
      engineUrl: '',
      engineErrors: ''
    };
  },
  methods: {
    loadSettings: function() {
      this.websiteUrl=localStorage.getItem("SsegWebsiteUrl")
      this.websiteKey=localStorage.getItem("SsegWebsiteKey")
      console.log("Opened settings")
      this.engineUrl= this.websiteUrl+'?code='+this.websiteKey
    },

    getSampleJob: async function () {

      var sentinelObject = JSON.parse(this.JsonString)
      var matchingValue = sentinelObject.body.object.properties.relatedEntities.find(a => a.id === this.selectedEntityId);

      // manipulate the incident to only contain one entity we are working on
      sentinelObject.body.object.properties.relatedEntities = [matchingValue]

      var sampleJob = {
        "item-type": "SOAR-trigger",
        "entity-type": this.entityTypeSelected,
        'query': 'xxx',
        "additional_params": {}
      }
      var payload = {
        'sentinel_incident': sentinelObject['body'],
        'enrichment_job': sampleJob
      }

      payload = JSON.stringify(payload)

      var our_headers = {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
      const req = await fetch(this.engineUrl, {
        method: 'POST',
        headers: our_headers,
        body: payload
      })
        .then((response) => {
          if (!response.ok) {
            console.warn("response may not be ok\n" + response)
            this.engineErrors = "Non 200 response - Check dev-tools.\n"

          }
          console.log(response)
          return response.json()
        })
        .catch((e) => {
          const error = "API error: " + e
          this.engineErrors = error
        });

      const reply = await req
      this.engineErrors += reply['errors'] + "\n" + reply['warnings']
      console.log(reply)
      this.sampleEntityValue = reply['customised_entities'][0]['entity_value']

/** 
      const xhr = new XMLHttpRequest();
      xhr.open("POST", this.engineUrl, true);

      // Send the proper header information along with the request
      xhr.setRequestHeader("Content-Type", 'application/json');
      xhr.setRequestHeader("Accept", 'application/json');


      xhr.onreadystatechange = () => {
        // Call a function when the state changes.
        if (xhr.readyState === XMLHttpRequest.DONE && xhr.status === 200) {
          // Request finished. Do processing here.
          var resp = JSON.parse(this.responseText)
          this.sampleEntityValue = resp['customised_entities'][0]['entity_value']
        }
      };
      xhr.send(payload);
**/


    }


  },
  mounted() {
    this.loadSettings()

    if (this.JsonString && this.selectedEntityId) {
      this.getSampleJob()
    }

  }

}



</script>

