<template>
  <div style="margin-left:25px">

    <h2 v-if="!this.engineErrors && !this.engineWarnings">You're ready to go!</h2>
    <h2 v-if="this.engineErrors || this.engineWarnings">Something doesn't look right:</h2>

    <p class="nomargin warningsurround" v-if="this.engineErrors"
      style="font-weight: bold; text-transform: uppercase; background-color: darkred;">{{ this.engineErrors }} </p>
    <p class="nomargin warningsurround" v-if="this.lintErrors"
      style="font-weight: bold; text-transform: uppercase; background-color: darksalmon;">{{ this.lintErrors }} </p>
    <p class="nomargin warningsurround" v-if="this.engineWarnings"
      style="font-weight: bold; text-transform: uppercase; background-color: darksalmon">{{ this.engineWarnings }}
    </p>


    <div v-show="!this.engineErrors">
      <h4>What's next</h4>

      <!--Display in table next to eachother-->

      <div style="width: 60%; display: inline-flex;  border-right: 2px solid grey; padding-right: 10% ">
        <ul>
          <li>Go to Sentinel</li>
          <li>Select Automation</li>
          <li>Create a new automation rule</li>
          <li>Specify the parameters of when to run the SOAR job</li>
          <li>Ensure to add the referenced job tag below</li>
          <p style="font-size: 0.8em; margin-top:15px"><i>Note that if you do not have an existing automation
              respsonsible
              for handling running the SOAR automation back-end, you may also need to select an additional action to run
              a
              playbook within your newly created automation rule.</i></p>
        </ul>
      </div>

      <div
        style="width: calc( 30% - 2px); display: inline-flex; justify-content: center;   align-content: center; height: inherit;">
        <ArmTemplateGenerator :job="this.displayPayload" />
      </div>

      <h3>Job tag</h3>
      <div>
        <pre style="white-space: pre-wrap;">{{ this.displayPayload }}
    </pre>
      </div>



    </div>


  </div>


</template>

<script>

import ArmTemplateGenerator from '@/components/ArmTemplateGenerator.vue'

export default {
  name: 'FinishWizard',
  props: {
    AdditionalParams: {
      type: String,
      default: "{}",
      required: false
    },
    Query: {
      type: String,
      default: "null",
      required: true
    },
    entityTypeSelected: {
      type: String,
      default: "null",
      required: true
    },
    JsonString: {
      type: String,
      default: null,
      required: true
    }

  },
  components: { ArmTemplateGenerator },
  data() {
    return {
      websiteUrl: '',
      websiteKey: '',
      displayPayload: "",
      engineUrl: '',
      engineErrors: '',
      engineWarnings: '',
      lintErrors: '',
      bypassWarnings: ['no entities in incident'],

    };
  },
  methods: {

    loadSettings: function () {
      this.websiteUrl = localStorage.getItem("SsegWebsiteUrl")
      this.websiteKey = localStorage.getItem("SsegWebsiteKey")
      console.log("Opened settings")
      this.engineUrl = this.websiteUrl + '?code=' + this.websiteKey
    },


    makePayload: function () {

      var params = JSON.parse(this.AdditionalParams)


      var sampleJob = {
        "item-type": "SOAR-trigger",
        "entity-type": this.entityTypeSelected,
        'query': this.Query,
        "additional_params": params
      }

      var payload = JSON.stringify(sampleJob, null, 2)
      this.displayPayload = payload



    },

    validateJob: async function (job_details) {

      var sentinelObject = {}


      var payload = {
        'sentinel_incident': sentinelObject,
        'enrichment_job': job_details
      }



      // manual lint logic
      if (payload['enrichment_job']['additional_params']['severity_change'] != undefined && payload['enrichment_job']['additional_params']['severity_change'].length > 1) {
        this.lintErrors += "Multiple severity changes specified in one SOAR job can cause conflict. The highest severity specified will override any other specified in the job."
      }


      payload = JSON.stringify(payload)

      var our_headers = {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
      const req = await fetch(this.engineUrl, {
        method: 'POST',
        headers: our_headers,
        body: payload
      })
        .then((response) => {
          if (!response.ok) {
            console.warn("response may not be ok\n" + response)
            this.engineErrors = "Non 200 response - Check dev-tools.\n"

          }
          response = response.json()
          return response
        })
        .catch((e) => {
          const error = "API error: " + e
          this.engineErrors = error
        });

      const reply = await req

      // filter out errors
      if (!this.bypassWarnings.includes(reply['warnings'])) {
        this.engineWarnings = reply['warnings']
      }
      this.engineErrors = reply['errors']
      console.log("validation result", reply)



    }

  },
  async mounted() {
    this.loadSettings()
    this.makePayload()
    this.validateJob(JSON.parse(this.displayPayload))

  }

}



</script>

