<template>
  <div>
    <h4>COMMENT ON INCIDENT</h4>

    <p class="note lowmargin">Where a query is specified in the SOAR enrichment job, a handful of results will be commented back to the incident by default, unless turned off below</p>

    <label class="switch" name="extensionEnabled" value="extensionEnabled"
      style="vertical-align: middle; margin-left: 4px;">
      <input v-model="this.commentOnIncidents" type="checkbox" id="extensionEnabled" checked @change="commentIncidentCallback">
      <span class="slider round"></span>
    </label>
    <label style="vertical-align: middle; margin-left: 5px;" for="extensionEnabled"> Comment on incidents </label>

  </div>
</template>

<script>
export default {
  name: 'CommentIncident',
  props: {
  },
  data() {
    return {
      commentOnIncidents:true
    };
  },
  methods: {
    // returns a dict that can be used to configs from parent components
    commentIncidentCallback: function () {

        this.$emit("commentConf",{
          type:"commentOnIncidents",
          value:this.commentOnIncidents})

    }
  }

}
</script>

