<template>

  <div style=" text-align: center;vertical-align: middle; align-content: center;">
    <p><b>Or generate an ARM template:</b></p>
    <img v-on:click="this.downloadArm()" style="cursor: pointer;" src="https://aka.ms/deploytoazurebutton"
      alt="Deploy to Azure Button" />

    <p style="font-size: 0.8em;"><i>Currently in beta</i></p>

  </div>



</template>

<script>


export default {
  name: 'ArmTemplateGenerator',
  props: {
    job: {
      type: String,
      default: "{}",
      required: false
    },

  },
  data() {
    return {
      displayPayload: {},


    };
  },
  methods: {

    generateUUID: function () {
      return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
        var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8);
        return v.toString(16);
    });
    },

    makeArArm: function () {

      var automationRuleName = "Run SOAR trigger on Incident"
      var automationRuleGuid = this.generateUUID()
      var stringiedSoarJob = this.job

      console.log("uuid", automationRuleGuid)


      var defaultTemplate = {
        "$schema": "https://schema.management.azure.com/schemas/2019-04-01/deploymentTemplate.json#",
        "contentVersion": "1.0.0.0",
        "parameters": {
          "workspace": {
            "type": "String"
          }
        },
        "resources": [
          {
            "id": `[concat(resourceId('Microsoft.OperationalInsights/workspaces/providers', parameters('workspace'), 'Microsoft.SecurityInsights'),'/AutomationRules/${automationRuleGuid}')]`,
            "name": `[concat(parameters('workspace'),'/Microsoft.SecurityInsights/${automationRuleGuid}')]`,
            "type": "Microsoft.OperationalInsights/workspaces/providers/AutomationRules",
            "apiVersion": "2022-12-01-preview",
            "properties": {
              "displayName": automationRuleName,
              "order": 99,
              "triggeringLogic": {
                "isEnabled": true,
                "triggersOn": "Incidents",
                "triggersWhen": "Created",
                "conditions": []
              },
              "actions": [
                {
                  "order": 99,
                  "actionType": "ModifyProperties",
                  "actionConfiguration": {
                    "severity": null,
                    "status": null,
                    "classification": null,
                    "classificationReason": null,
                    "classificationComment": null,
                    "owner": null,
                    "labels": [
                      {
                        "labelName": stringiedSoarJob,
                        "labelType": "User"
                      }
                    ]
                  }
                }
              ]
            }
          }
        ]
      }


      return defaultTemplate

    },

    downloadArm: function () {
      this.makeArArm()
      const a = document.createElement('a');
      const type = "SSEG_Arm.json".split(".").pop();
      var text = JSON.stringify(this.makeArArm())


      a.href = URL.createObjectURL(new Blob([text], { type: `text/${type === "txt" ? "plain" : type}` }));
      a.download = "SSEG_Arm.json";
      a.click();

    }
  },

  async mounted() {
  }

}



</script>

