    <!--Container for SOAR options-->
    <div style="height: 55vh; margin-top: 15px">
      <h4 style="margin: 0px; margin-bottom: 3px;">Action Categories</h4>

      <div style="width:auto ; max-width: 200px; height: 100%; display: inline-table; ">
        <button :class="this.selectedDivName == 'increaseSev' ? 'soarCat selectedCategory' : 'soarCat'"
          @click="this.selectedDivName = 'increaseSev'">

          <div style="display: inline-block; vertical-align: middle;">
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
              stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"
