              <path d="M10.29 3.86L1.82 18a2 2 0 0 0 1.71 3h16.94a2 2 0 0 0 1.71-3L13.71 3.86a2 2 0 0 0-3.42 0z"></path>
              <line x1="12" y1="9" x2="12" y2="13"></line>
              <line x1="12" y1="17" x2="12.01" y2="17"></line>
            </svg>
          </div>

          <div style="display: inline-block; vertical-align: middle; margin-top: 5px;">
            &nbsp; Increase Severity
          </div>
        </button>


        <button :class="this.selectedDivName == 'comments' ? 'soarCat selectedCategory' : 'soarCat'"
          @click="this.selectedDivName = 'comments'">

          <div style="display: inline-block; vertical-align: middle;">

            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
              stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"
              class="feather feather-message-square">
              <path d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z"></path>
            </svg>
          </div>

          <div style="display: inline-block; vertical-align: middle; margin-top: 5px;">

            &nbsp; Comment on Incident
          </div>
        </button>
      </div>

      <!-- Select Item area -->
      <div
        style="display: inline-table; width: max-content; height: 100%; width: calc( 99% - 200px ); overflow: hidden;">
        <div class="selectedCategory divOption">
          <SeverityChange @severityConf="(val) => updateAdditionalParams(val)"
            v-if="this.selectedDivName == 'increaseSev'" />
          <CommentIncident @commentConf="(val) => updateAdditionalParams(val)"
            v-if="this.selectedDivName == 'comments'" />

        </div>



      </div>


    </div>



  </div>
</template>

<script>
import SeverityChange from '@/components/SOARActions/SeverityChange.vue'
import CommentIncident from '@/components/SOARActions/CommentIncident.vue'

export default {
  name: 'SpecifySOARActions',
  components: { SeverityChange, CommentIncident },
  props: {
    queryText: {
      type: String,
      default: null,
      required: false
    }
  },
  data() {
    return {
      selectedDivName: 'increaseSev',
      currentAdditionalParams: {}
    };
  },
  methods: {
    emitAdditionalParamsParent: function () {
      this.$emit("additionalParams", this.currentAdditionalParams)

    },

    updateAdditionalParams: function (newParam) {
      var paramType = newParam['type']
      this.currentAdditionalParams[paramType] = newParam['value']
      this.emitAdditionalParamsParent()
    }

  }
}
</script>

