<template>
  <div style="margin-left:25px">

    <h2>Select Entity Type</h2>

    <!-- If errors-->
    <div v-if="this.currentErrors.length > 0">
      <p v-for="item in this.currentErrors" :key="item" style="color: red;"> {{ item }} </p>

      <p style="font-size: 0.8em;"> Select try again or go back and change the JSON payload </p>

      <button class="retry" :onclick="this.main"><i>Retry</i></button>
    </div>

    <p v-if="this.sentinelEntities != undefined && this.sentinelEntities.length > 0"
      style="color: grey; font-size: 0.8em;"> &#9432; <i>Note that this entity list has been generated locally. Entity
        manipulation may occur within the SOAR engine to take a more accurate value, such as using a User Principal Name
        versus a Display Name. A simulated result will be displayed to you before the wizard completes. </i></p>

    <p v-if="this.sentinelEntities != undefined && this.sentinelEntities.length > 0"> Select an entity type to configure
      a query against and then hit next to continue. Use advanced features at the bottom if you already know what entity
      type to use.</p>

    <!-- Entities section-->
    <br>
    <div v-for="item in sentinelEntities" v-bind:key="item" class="entityList">
      <p style="font-weight: bolder; color: lightblue; margin-top: 0px; margin-bottom: 0px; display: inline-block">{{
      item.properties.friendlyName }} </p>
      <div style="margin-left: 20px;">
        <p class="entitySubtype"><b>kind:</b> '{{ item.kind }}'</p>
        <p class="entitySubtype"><b>advanced properties:</b> {{ item.properties }}</p>
        <button class="entityButton" @click="$emit('selectedEntityType', { 'kind': item.kind, 'entityId': item.id })"
          style="display: inline-block;">Select this entity type: <b>{{ item.kind }} </b> &#8680;
        </button>
      </div>

    </div>


    <hr>
    <h2>Advanced</h2>

    <p style="vertical-align: middle; display: inline-block;">Manual Sentinel entity type not listed here: &nbsp;</p>
    <select name="manualEntity" id="ManualEntity" v-model="this.customSelectedEntityType">
      <option value="host">host</option>
      <option value="ip">ip</option>
      <option value="url">url</option>
      <option value="account">account</option>
      <option value="filehash">filehash</option>
      <option value="dns">dns</option>
      <option value="azure-resource">azure-resource</option>
      <option value="mailCluster">mailCluster</option>
      <option value="mailMessage">mailMessage</option>
      <option value="mailbox">mailbox</option>
      <option value="registry-key">registry-key</option>
      <option value="file">file</option>
    </select>
    <button class="entityButton" style="display: inline-block; margin-bottom: 30px; margin-top:30px;"
      @click="$emit('selectedEntityType', { 'kind': this.customSelectedEntityType, 'entityId': '' })">Select this type &#8680;
      
      <div style="display: inline-block">

      </div>
    </button>


  </div>


</template>

<script>


export default {
  name: 'GetSentinelEntitiesJson',
  props: {
    JsonString: {
      type: String,
      default: null,
      required: true
    },
  },
  data() {
    return {
      currentErrors: [],
      sentinelEntities: {},
      customSelectedEntityType: ''
    };
  },
  methods: {
    getSentinelEntities: function (objStr) {

      this.sentinelEntities = {}

      try {
        var incident = JSON.parse(objStr);
        var entities = incident.body.object.properties.relatedEntities
      } catch (e) {
        this.currentErrors.push("Does not appear to be JSON for a Sentinel incident. Make sure to include the full output in from the incident trigger logic app outputs. ")
        console.error(e)
        return
      }

      console.log(entities)
      this.sentinelEntities = entities



    },
    main: function () {
      this.currentErrors = [] // reset
      // a series of checks performed when this component is mounted
      try {
        JSON.parse(this.JsonString);
      } catch (e) {
        this.currentErrors.push(" Invalid JSON - go back and enter a correct payload (include the full output from the incident trigger). ")
        return
      }
      // continue if not early return
      this.getSentinelEntities(this.JsonString)
    }

  },
  mounted() {
    this.main()
  }

}



</script>

