<template>
  <div style="margin-right: 30px;">

    <p></p>

    <p :class="this.listComponents[this.currentComponentIndex] == item ? 'listTab listTabSelect' : 'listTab'"
      v-for="item in this.listComponents" v-bind:key="item">{{ item }}</p>

    <div style="margin-left: 10px; height: 75vh; overflow: scroll;">
      <HelloSentinelJson @jsonIncidentChange="(val) => this.sentinelJson = val"
        v-if="this.listComponents[this.currentComponentIndex] == 'Enter Sentinel JSON'">
      </HelloSentinelJson>

      <GetSentinelEntitiesJson v-if="this.listComponents[this.currentComponentIndex] == 'Get Incident Entities'"
        :JsonString="this.sentinelJson" @selectedEntityType="(val) => updateEntityValues(val)">
      </GetSentinelEntitiesJson>
      <CreateQuery v-if="this.listComponents[this.currentComponentIndex] == 'Specify Query'"
        :JsonString="this.sentinelJson" :selectedEntityId="this.entityIdSelected"
        :entityTypeSelected="this.entityTypeSelected" @queryText="(val) => this.queryText = val"></CreateQuery>
      <SpecifySOARActions v-if="this.listComponents[this.currentComponentIndex] == 'Specify SOAR Action'"
        :queryText="this.queryText" @additionalParams="(val) => updateAdditionalParams(val)"></SpecifySOARActions>

      <FinishWizard v-if="this.listComponents[this.currentComponentIndex] == 'Finish'"
        :AdditionalParams="JSON.stringify(this.additionalParams)" :Query="this.queryText" :entityTypeSelected="this.entityTypeSelected" :JsonString="this.sentinelJson">
      </FinishWizard>
    </div>



    <!-- Dynamic buttons for back and forth -->
    <div style="margin-left: 10px">

      <button class="navComponents" v-if="this.currentComponentIndex > 0" v-on:click="this.decrementTab"> Previous: {{
        this.listComponents[this.currentComponentIndex - 1] }} </button>

      <!-- Add custom conditions of when to show the button or requires input -->
      <button class="navComponents"
        v-if="this.listComponents[this.currentComponentIndex + 1] != undefined && this.listComponents[this.currentComponentIndex] != 'Get Incident Entities'"
        v-on:click="this.incrementTab">Next:
        {{ this.listComponents[this.currentComponentIndex + 1] }}</button>

    </div>

  </div>
</template>

<script>
import HelloSentinelJson from '@/components/EnterSentinelJson.vue'
import GetSentinelEntitiesJson from '@/components/GetSentinelEntitiesJson.vue'
import CreateQuery from '@/components/CreateQuery'
import SpecifySOARActions from '@/components/SpecifySOARActions.vue'
import FinishWizard from '@/components/FinishWizard.vue'
export default {
  name: 'HelloWorld',
  components: { HelloSentinelJson, GetSentinelEntitiesJson, CreateQuery, SpecifySOARActions, FinishWizard },
  props: {
    msg: String
  },
  data() {
    return {
      currentComponentIndex: 0,
      listComponents: ['Enter Sentinel JSON', 'Get Incident Entities', 'Specify Query', 'Specify SOAR Action', 'Finish'],
      // capture emit updates that go into value
      sentinelJson: '',
      entityTypeSelected: '',
      entityIdSelected: '',
      queryText: '',
      additionalParams: {}
    };
  },
  methods: {
    incrementTab: function () {
      if (this.currentComponentIndex != this.listComponents.length) {
        this.currentComponentIndex += 1
      }
    },
    decrementTab: function () {
      if (this.currentComponentIndex > 0) {
        this.currentComponentIndex -= 1
      }
    },
    updateEntityValues: function (updateDict) {
      console.log(updateDict)
      this.entityIdSelected = updateDict.entityId
      this.entityTypeSelected = updateDict.kind
      // once updated values then move on
      this.incrementTab()
    },
    updateAdditionalParams: function (params) {
      console.log("Recieved in Hello World", params)
      this.additionalParams = params
      console.log(this.additionalParams)
    }
  }
}
</script>

